import React from 'react';

import classNames from 'classnames';
import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import Icon from '@material-ui/core/Icon';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import withStyles from '@material-ui/core/styles/withStyles';

import firebase from './../firebase.js';
import $ from 'jquery';
import moment from 'moment';

const styles = theme => ({
    root: {
      width: '100%',
    },
    layout: {
        width: 'auto',
        display: 'block', // Fix IE 11 issue.
        marginLeft: theme.spacing(3),
        marginRight: theme.spacing(3),
        [theme.breakpoints.up(400 + theme.spacing(3) * 2)]: {
          width: 400,
          marginLeft: 'auto',
          marginRight: 'auto',
        },
      },
      paper: {
        marginTop: theme.spacing(3),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: `${theme.spacing(2)}px ${theme.spacing(3)}px ${theme.spacing(3)}px`,
      },
      avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
      },
      form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
      },
      submit: {
        marginTop: theme.spacing(3),
      },
      centeralign: {
        flexGrow: 1,
        textAlign: 'center',
      },
      contentbody: {
        height: '85vh',
      }
});

class Register extends React.Component {
    constructor(props) {
        super(props);

        this.name = React.createRef();
        this.nickname = React.createRef();
        this.phone = React.createRef();
        this.email = React.createRef();
        this.password = React.createRef();
    }

    state = {
        errorlogin: '',
    }

    componentDidMount() {
      $("div.container").css({display:'none'});
    }

    register = (e) => {
        e.preventDefault();
        
        const auth = firebase.auth();
        try{
          auth.createUserWithEmailAndPassword(this.email.current.value,this.password.current.value)
            .then(() => { 
              //setTimeout(function(){
              //try{
                this.writeMessageToDB();
              //}catch(err){
                //setTimeout(function(){ 
                //alert("Error : ", err);
                //}.bind(this), 3000);
              //}
              //}.bind(this), 10000);
              //window.location.href = "/reactchat/login"; 
            })
            .catch(error => {   
              switch(error.code) {
                case 'auth/email-already-in-use':
                      alert('Email already in use !');
                      break;
                default:
                  break;
            }
          });
        }catch(err){
            alert("Error : ", err);
        }
        //setTimeout(function(){ 
        //  window.location.href = "/reactchat/login";
        //}.bind(this), 3000);
        }

    writeMessageToDB = () => {
          var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
          var name = this.name.current.value;
          var email = this.email.current.value;
          var nickname = this.nickname.current.value;
          var phone = this.phone.current.value;
          var role = 'demo';
          //alert(curtime);
          //alert(name);
          //alert(email);
          //alert(nickname);
          //alert(phone);
          //let data = ({name: name,email: email,nickname: nickname,phone: phone,time: curtime });
          //alert(JSON.stringify(data));
          //alert(JSON.stringify(firebase.database().ref("/usersdetails/a")));
          //setTimeout(function(){ 
          //var ddbbref;
          //var dbref = firebase.database().ref('/users');
          
          //dbref.push({
          //    'name': name,
          //    'email': email,
          //    'nickname': nickname,
          //    'phone': phone,
          //    'time': curtime,
          //    'role': role
          //  }).set();
          firebase.database().ref('/users').push({
              'name': name,
              'email': email,
              'nickname': nickname,
              'phone': phone,
              'time': curtime,
              'role': role,
              'terms':0,
              'password': this.password.current.value
            }).set();
          
            //alert(JSON.stringify(ddbbref));
          //}.bind(this), 3000);
          window.location.href = "/reactsopcast/";
        }

    render() {
        const { classes } = this.props;

        return (
            <div style={{ height: '100%', position: 'relative',zIndex: '100' }}>
            <h2>Register</h2>
            <hr/>
            <React.Fragment>
            <CssBaseline />
            <main className={classNames(classes.layout,"register")}>
            <Paper className={classes.paper}>
                <Avatar className={classes.avatar}>
                <Icon className={classNames(classes.icon, 'fa fa-user-plus')} />
                </Avatar>
                <Typography component="h1" variant="h5">
                Register
                </Typography>
                <div className={classes.errorlogin}>{this.state.errorlogin}</div>
                <form className={classNames(classes.form,"register")} method="post" onSubmit={this.register.bind(this)}>
                <FormControl margin="normal" required fullWidth>
                    <InputLabel htmlFor="name">User Name</InputLabel>
                    <Input
                    type="text"
                    id="name" 
                    name="name"
                    inputRef={this.name}
                    />
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                    <InputLabel htmlFor="name">Nick Name</InputLabel>
                    <Input
                    type="text"
                    id="nickname" 
                    name="nickname"
                    inputRef={this.nickname}
                    />
                </FormControl>
                <FormControl margin="normal" fullWidth>
                    <InputLabel htmlFor="name">Phone Number</InputLabel>
                    <Input
                    type="text"
                    id="phone" 
                    name="phone"
                    inputRef={this.phone}
                    />
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                    <InputLabel htmlFor="email">Email Address</InputLabel>
                    <Input
                    type="email"
                    id="email" 
                    name="email"
                    inputRef={this.email}
                    />
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                    <InputLabel htmlFor="password">Password</InputLabel>
                    <Input
                    name="password"
                    type="password"
                    id="password"
                    inputRef={this.password}
                    />
                </FormControl>
                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                >
                    Register
                </Button>
                </form>
            </Paper>
            </main>
        </React.Fragment>
            </div>
         );
        }
    }
    
    export default withStyles(styles)(Register);
        